.description__wrapper {
  display: flex;
  column-gap: 7rem;

  // padding: 4rem 0;
  border-bottom: 1px solid $gainsboro;

  @media (max-width: $vp-968) {
    flex-direction: column;
    row-gap: 2rem;
  }
}

.description__image {
  width: 358px;
  height: 232px;
  margin-left: -127px;
  object-fit: cover;
  flex-shrink: 0;

  @media (max-width: $vp-968) {
    align-self: center;

    width: 295px;
    height: 192px;
    margin-left: 0;
  }
}

.description__image img {
  width: 100%;
  height: 100%;
}

.description h2 {
  font-weight: 700;
  font-size: 2.6rem;
  line-height: 1.5;
  color: $black;

  margin: 0 0 2rem;

  @media (max-width: $vp-968) {
    font-size: 2.4rem;
  }
}

.description p {
  font-size: 1.6rem;
  line-height: 1.5;
  margin: 0 0 1.5rem;

  &:last-child {
    margin: 0;
  }

  @media (max-width: $vp-968) {
    font-size: 1.4rem;
  }
}
